.input-field {
  max-width: 500px;
  width: 100%;
  height: 56px;
  padding-right: 30px;
  padding-left: 30px;
  border-radius: 56px;
  border-width: 2px;
  border-color: $gray-400;
  background-color: $white;
  box-shadow: none;
  border-style: solid;
  color: $gray-650;
  font-family: "Raleway", "Helvetica", "Arial", sans-serif;
  font-size: 15px;

  &_small {
    height: 40px;
    padding-right: 15px;
    padding-left: 15px;
    border-radius: 20px;
  }

  &_red {
    border-color: $red-100;
    background-color: $red-300;
    color: $white;

    &::-moz-placeholder {color: $white;}
    &::-webkit-input-placeholder {color: $white;}
    &::-ms-input-placeholder {color: $white;}
    &::placeholder {color: $white;}
  }

  &_border-transparent {
    border-color: transparent;
  }

  &_underlined {
    height: 23px;
    padding-right: 5px;
    padding-left: 5px;
    border-radius: 0;
    border-width: 0 0 1px 0;
    border-color: $gray-400;
    color: $gray-700;
    font-weight: bold;
    text-align: center;

    &:hover,
    &:focus {
      border-color: $gray-500;
    }
  }

  &__icon {
    position: absolute;
    top: 50%;
    right: 0.75em;
    width: 1.5em;
    height: 1.5em;
    transform: translateY(-50%);
  }

  &__select {
    padding-right: 2em;
    background-color: transparent;
    cursor: pointer;

    & + .input-field__icon {
      z-index: -1;
    }
  }

  &__wrapper {
    display: inline-block;
    position: relative;
    color: $gray-650;
  }

  &:focus,
  &:hover {
    border-color: $gray-500;
  }
}
