.product-types {
  position: relative;
  padding-top: 60px;
  padding-bottom: 30px;
  background: $gray-200;

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    bottom: 100%;
    right: 0;
    background-repeat: repeat;
    height: 10px;
    background-size: 20px 20px;
    background-image:
      radial-gradient(circle at 10px -10px,transparent 13px,#f8f7f2 13px);
    z-index: 2;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
  }

  &__list-item {
    flex-basis: 25%;
    flex-shrink: 0;
    margin-bottom: 35px;
  }

  &__link {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-right: 45px;
    color: $gray-700;
    font-style: 15px;
    font-weight: 600;
    text-decoration: none;

    &:hover,
    &:focus {
      text-decoration: underline;
    }
  }

  &__icon {
    flex-shrink: 0;
    width: 55px;
    height: 55px;
    margin-right: 30px;
    background-position: center center;
    background-size: 80% 80%;
    background-repeat: no-repeat;
    transition: background-size 0.2s;
  }
}

@media screen and (max-width: 1343px) {
  .product-types {
    &__list-item {
      flex-basis: 33.3333%;
    }

    &__link {
      padding-right: 85px;
    }
  }
}

@media screen and (max-width: 1199px) {
  .product-types {
    padding-top: 50px;
    padding-bottom: 20px;

    &__link {
      padding-right: 0;
    }
  }
}

@media screen and (max-width: 991px) {
  .product-types {
    &__list-item {
      flex-basis: 50%;
    }

    &__link {
      padding-right: 60px;
    }

    &__icon {
      width: 65px;
      height: 65px;
    }
  }
}

@media screen and (max-width: 767px) {
  .product-types {
    padding-bottom: 40px;

    &__list-item {
      margin-bottom: 15px;
    }

    &__link {
      padding-right: 15px;
    }

    &__icon {
      width: 6px;
      height: 6px;
      margin-right: 15px;
      border: 2px solid $red-200;
      border-radius: 50%;
    }
  }
}

@media screen and (max-width: 575px) {
  .product-types {
    padding-bottom: 60px;

    &__list-item {
      flex-basis: 100%;

      &:nth-of-type(n + 9) {
        display: none;
      }
    }

    .button {
      margin-top: 20px;
      margin-right: auto;
      margin-left: auto;
    }
  }
}
